/* Resetting styles */
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  box-sizing: border-box;
}

.wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.wrapper-large {
  max-width: 100%;
  padding: 0 32px;
}

a:visited {
  color: initial;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  margin-bottom: 0.5em;
}

ul {
  padding-left: 16px;
}

label, input[type=text], input[type=number], textarea, select {
  display: block;
  width: 100%;
  margin: 0;
}

label {
  margin-bottom: 12px;
  padding: 0;
}

input[type=text], input[type=number], textarea, select, fieldset {
  margin-top: 4px;
  padding: 8px;
  border: 1px solid #d0d7de;
  border-radius: 3px;
  background: #fff;
}

.w-md-editor {
  margin-top: 8px;
}

fieldset {
  margin-left: 0;
  margin-right: 0;

  & > .option {
    display: flex;
    align-items: center;
    margin-bottom: 4px;
  }
}

button {
  appearance: none;
  cursor: pointer;
  border: none;
  background-color: #333;
  color: #fff;
  border-radius: 3px;
  padding: 5px 8px;

  &:focus {
    background-color: #171717;
  }
}

button.save {
  font-size: 16px;
  padding: 12px;
  width: 100%;
}

button.delete {
  background-color: #911313;
}

hr {
  margin: 16px auto;
  border: none;
  border-top: 2px solid #d0d7de;
  width: 50%;
}

.row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 8px -4px;

  & > * {
    margin-left: 4px;
    margin-right: 4px;
  }
}

.row-align-top {
  align-items: flex-start;
}

.width-forty {
  width: 40%;
}

.width-sixty {
  width: 60%;
}

.helper {
  background: #e1e7ed;
  padding: 8px;
  border-radius: 3px;
  font-weight: 500;
}

.bold {
  font-weight: 600;
}

.wmde-markdown {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.wmde-markdown p {
  color: #000;
}